  justify-content: center;
  align-items: center;
  transition: all 0.3s;
}
.btn__blue span img {
  width: 16px;
  height: 16px;
  object-fit: contain;
  transition: all 0.3s;
}
.btn__blue::before {
  content: "";
  display: block;
  position: absolute;
  z-index: -1;
  width: 100%;
  height: 38px;
  border-radius: 500px;
  filter: blur(41.1575469971px);
  background: radial-gradient(102.92% 371.47% at 50% 50%, #5daddc 0%, rgba(93, 173, 220, 0.6) 64.48%, rgba(93, 173, 220, 0) 100%);
  opacity: 0.8;
  transition: all 0.3s;
  bottom: -8px;
  left: 0;
}
.btn__blue:hover {
  box-shadow: none;
}
.btn__blue:hover span img {
  transform: rotate(45deg);
}
.btn__blue:hover::before {
  opacity: 0;
}

